:root {
    --padding: 20px;
    --margin: 20px;
    --radius: 10px;
    --background: linear-gradient(90deg, rgba(175,175,175,1) 0%, rgba(231,224,223,1) 52%, rgba(226,219,217,1) 62%, rgba(164,164,164,1) 92%);
    --items: rgb(245, 237, 220);
}

* {
    padding: 0;
    margin: 0;
}

body {
    width: 100%;
    height: 100%;
    background: var(--background); 
}

#wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

#header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

h1 {
    font-size: 60px;
    margin-top: 40px;
}

button {
    width: 30px;
}